<template><section><h1>hea-ui内置动画</h1>
<hr>
<p>hea-ui提供了丰富的动画效果，其中包括fade动画，slide动画，zoom动画，旋转动画，以及其他动画，其中使用方法简单具体用法如下</p>
<br>
<pre v-pre=""><code v-pre="" class="language-html">	
<span class="hljs-tag">&lt;<span class="hljs-name">transition</span> <span class="hljs-attr">name</span>=<span class="hljs-string">"slide"</span>&gt;</span>
	<span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">v-if</span>=<span class="hljs-string">"..."</span>&gt;</span>
		...
	<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">transition</span>&gt;</span>

</code></pre>
<p>其中 <code v-pre="" class="code_inline">name</code> 属性是 <a href="#ani">现有动画库</a> 名称</p>
